<template>
  <div class="home-container">
    <div class="header">
      <div class="logo">
        系统管理后台
        <a @click="outdentClick">
          <i class="fa " v-bind:class="[isCollapse ? 'fa-indent' : 'fa-outdent']"></i>
        </a>

      </div>

      <div class="toolbar">
        <a>超级管理员</a>
        <span class="out-box" @click="loginOut">
          <i class="fa fa-sign-out"></i>
        </span>
      </div>
    </div>
    <div class="menu" v-bind:style="{'width':menuWidth +'px'}">
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        :collapse="isCollapse">
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span>参考案例</span>
          </template>
          <el-menu-item index="1-1">
            <router-link to="/home/table" tag="li">table</router-link>
          </el-menu-item>
          <el-menu-item index="1-2">
            <router-link to="/home/upload" tag="li">upload</router-link>
          </el-menu-item>
          <el-menu-item index="1-3">
            <router-link to="/home/table" tag="li">基础demo3</router-link>
          </el-menu-item>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span slot="title">导航二</span>
          </template>
          <el-menu-item index="2-1">选项1</el-menu-item>
          <el-menu-item index="2-2">选项2</el-menu-item>
          <el-menu-item index="2-3">选项3</el-menu-item>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-setting"></i>
            <span slot="title">导航三</span>
          </template>

          <el-menu-item index="3-1">选项1</el-menu-item>
          <el-menu-item index="3-2">选项2</el-menu-item>
          <el-menu-item index="3-3">选项3</el-menu-item>

        </el-submenu>
      </el-menu>
    </div>
    <div class="app-container" v-bind:style="{'margin-left': marginLeft +'px'}">
      <transition name="fade" mode="out-in">
        <!-- <router-view :key="key"></router-view> -->
        <router-view></router-view>
      </transition>

    </div>
  </div>
</template>

<script>
  import {isvalidUsername} from '@/utils/validate'
  import {getToken, setToken, removeToken} from '@/utils/auth'

  export default {
    name: 'home',
    data() {
      return {
        loading: false,
        pwdType: 'password',
        isCollapse: false,
        menuWidth: 180,
        marginLeft: 195
      }
    },
    methods: {
      loginOut(){
        removeToken();
        this.$router.push({path: '/login'})
      },
      outdentClick(){
        if (this.isCollapse == false) {
          this.isCollapse = true;
          this.menuWidth = 56;
          this.marginLeft = 70
        } else {
          this.isCollapse = false;
          this.menuWidth = 180;
          this.marginLeft = 195
        }
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }

</script>

<style rel="stylesheet/scss" lang="scss">
  @import "src/styles/mixin.scss";
  @import "./index.scss";
</style>



