<template>
  <div class="table-container">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>活动管理</el-breadcrumb-item>
      <el-breadcrumb-item>活动列表</el-breadcrumb-item>
      <el-breadcrumb-item>活动详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search-box">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="关键字">
          <el-input v-model="formInline.user" placeholder="关键字"></el-input>
        </el-form-item>

        <el-form-item label="分类">
          <el-select v-model="formInline.region1" placeholder="请选择" @change="select1change">
            <el-option
              v-for="item in options1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select v-model="formInline.region2" placeholder="请选择" @change="select2change">
            <el-option
              v-for="item in options2"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select v-model="formInline.region3" placeholder="请选择">
            <el-option
              v-for="item in options3"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getData(1)">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="handle-box">
      <el-button type="primary" @click="dialogVisible = true">添加</el-button>
      <el-button type="danger" :disabled="multipleSelection.length==0">删除所选</el-button>
    </div>
    <el-table
      :data="list"
      v-loading.body="listLoading"
      element-loading-text="Loading"
      ref="multipleTable"
      @selection-change="handleSelectionChange"
      border fit highlight-current-row>
      <el-table-column
        type="selection"
        width="38">
      </el-table-column>
      <el-table-column align="center" label='ID' width="95">
        <template slot-scope="scope">
          {{scope.row.id}}
        </template>
      </el-table-column>
      <el-table-column align="center" label='缩略图' width="180">
        <template slot-scope="scope">
          <img :src="imgRUrl+scope.row.image" alt="" class="table-img">
        </template>
      </el-table-column>
      <el-table-column label="视屏名称">
        <template slot-scope="scope">
          {{scope.row.title}}
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="是否显示" width="110" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.isDisplayed">是</el-tag>
          <el-tag v-if="!scope.row.isDisplayed" type="info">否</el-tag>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="created_at" label="修改时间" width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.createTime}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width="200">
        <template slot-scope="scope">
          <div class="table-btn">
            <router-link :to="{ path: 'table_add', query: { id: scope.row.id }}">
              <el-button type="primary">编辑</el-button>
            </router-link>

            <el-button type="danger" plain @click="deleteOne(scope.row.id)">删除</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      background
      :page-sizes="[10, 20, 30, 40]"
      :page-size="listQuery.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="listQuery.total">
    </el-pagination>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
    >
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="活动名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="活动区域" prop="region">
          <el-select v-model="ruleForm.region" placeholder="请选择活动区域">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动时间" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date1"
                              style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-time-picker type="fixed-time" placeholder="选择时间" v-model="ruleForm.date2"
                              style="width: 100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="即时配送" prop="delivery">
          <el-switch v-model="ruleForm.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="活动性质" prop="type">
          <el-checkbox-group v-model="ruleForm.type">
            <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
            <el-checkbox label="地推活动" name="type"></el-checkbox>
            <el-checkbox label="线下主题活动" name="type"></el-checkbox>
            <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="特殊资源" prop="resource">
          <el-radio-group v-model="ruleForm.resource">
            <el-radio label="线上品牌商赞助"></el-radio>
            <el-radio label="线下场地免费"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="活动形式" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">重置</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {getList, getCategorys} from './table-server'
  import moment from "moment"
  export default {
    data() {
      return {
        list: null,
        listLoading: true,
        listQuery: {
          pageNumber: 1,
          pageSize: 10,
          total: 0
        },
        imgRUrl: process.env.imgReadUrl,
        formInline: {
          user: null,
          region1: null,
          region2: null,
          region3: null
        },
        options1: null,
        options2: null,
        options3: null,
        multipleSelection: [],
        dialogVisible: false,
        ruleForm: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        rules: {
          name: [
            {required: true, message: '请输入活动名称', trigger: 'blur'},
            {min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
          ],
          region: [
            {required: true, message: '请选择活动区域', trigger: 'change'}
          ],
          date1: [
            {type: 'date', required: true, message: '请选择日期', trigger: 'change'}
          ],
          date2: [
            {type: 'date', required: true, message: '请选择时间', trigger: 'change'}
          ],
          type: [
            {type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change'}
          ],
          resource: [
            {required: true, message: '请选择活动资源', trigger: 'change'}
          ],
          desc: [
            {required: true, message: '请填写活动形式', trigger: 'blur'}
          ]
        }
      }
    },
    created() {
      this.getData(1)
      this.getCategorys()
    },
    methods: {
      /*
       *数据获取
       */
      getData(pageNumber) {
        this.listLoading = true;
        let categoryId = null;
        if (this.formInline.region3) {
          categoryId = this.formInline.region3
        } else if (this.formInline.region2 && !this.formInline.region3) {
          categoryId = this.formInline.region2
        } else {
          categoryId = this.formInline.region1
        }
        ;
        let listQuery = {
          pageNumber: pageNumber,
          pageSize: this.listQuery.pageSize,
          categoryId: categoryId
        };
        getList(listQuery).then(response => {
          this.list = response.videoList;
          this.listQuery.total = response.total;
          for (let i = 0; i < response.videoList.length; i++) {
            response.videoList[i].createTime = moment(response.videoList[i].createTime).format('YYYY-MM-DD HH:mm:ss')
          }
          this.listLoading = false
        })
      },
      /*
       *分类操作
       */
      getCategorys(){
        getCategorys({}).then(response => {
          this.options1 = response.content
        })
      },

      select1change(key){
        for (let i = 0; i < this.options1.length; i++) {
          if (this.options1[i].id == key) {
            this.options2 = this.options1[i].subCategory
          }
        }
      },
      select2change(key){
        for (let i = 0; i < this.options1.length; i++) {
          for (let o = 0; o < this.options1[i].subCategory.length; o++) {
            if (this.options1[i].subCategory[o].id == key) {
              this.options3 = this.options1[i].subCategory[o].subCategory
            }
          }
        }
      },
      /*
       *多选操作
       */
      handleSelectionChange(val){
        let multipleSelection = []
        for (let i = 0; i < val.length; i++) {
          multipleSelection.push(val[i].id)
        }
        this.multipleSelection = multipleSelection
        console.log(this.multipleSelection)
      },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      /*
       *分页操作
       */
      handleSizeChange(val) {
        this.listQuery.pageSize = val;
        this.getData(1)
      },
      handleCurrentChange(val) {
        this.getData(val)
      },
      /*
       *删除
       */
      deleteOne(id){
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      /*
       *清空搜搜
       */
      clear(){
        this.formInline.region1 = null
        this.formInline.region2 = null
        this.formInline.region3 = null
        this.options2 = null
        this.options3 = null

      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss">
  @import "src/styles/mixin.scss";
  @import "./index.scss";
</style>



